import React from "react"
import {Layout as AntdLayout, Menu} from "antd";
import Form from "@jform/core"

const {Header, Footer, Sider, Content} = AntdLayout;

const items1 = ['1', '2', '3'].map((key) => ({
    key,
    label: `nav ${key}`,
}));
const items2 = [1, 2, 3].map((icon, index) => {
    const key = String(index + 1);
    return {
        key: `sub${key}`,
        label: `subnav ${key}`,
        children: new Array(4).fill(null).map((_, j) => {
            const subKey = index * 4 + j + 1;
            return {
                key: subKey,
                label: `option${subKey}`,
            };
        }),
    };
});

export const Layout = () => {
    return   <AntdLayout>
        <Header className="header">
            <div className="logo" />
            <Menu theme="dark" mode="horizontal"  items={items1} />
        </Header>
        <Content style={{ padding: '0 50px' }}>
            <AntdLayout className="site-layout-background" style={{ padding: '24px 0' }}>
                <Sider className="site-layout-background" width={200}>
                    <Menu
                        mode="inline"
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        style={{ height: '100%' }}
                        items={items2}
                    />
                </Sider>
                <Content style={{ padding: '0 24px', minHeight: 280 }}>
                    <Form onSubmit={console.log}
                          schema={{
                              title: 'Tell m',
                              type: 'object',
                              required: ['firstName'],
                              properties: {
                                  'image': {
                                      type: 'string',
                                      title: 'image'
                                  },
                                  user: {
                                      type: 'object',
                                      title: 'user',
                                      'properties': {
                                          'password': {
                                              'type': 'string',
                                              'title': 'Password'
                                          },
                                          'username': {
                                              'type': 'string',
                                              'title': 'username'
                                          }
                                      }
                                  },
                                  'details': {
                                      type: 'boolean',
                                      title: 'details'
                                  },
                                  'lastName': {
                                      'type': 'string',
                                      'title': 'Last name'
                                  },
                                  'bio': {
                                      'type': 'string',
                                      'title': 'Bio'
                                  },
                                  'firstName': {
                                      'type': 'string',
                                      'title': 'First name'
                                  },
                                  'age': {
                                      'type': 'string',
                                      'title': 'Age'
                                  }
                              }
                          }}
                          data={{}}
                          configSchema={{
                              widget: {
                                  layout: [
                                      {
                                          firstName: {md: 6},
                                          lastName: {md: 6, optional: ({isFilled}) => isFilled('firstName')}
                                      }, {
                                          image: {md: 3, optional: ({isFilled}) => isFilled('lastName')},
                                          user: {md: 9, optional: ({isFilled}) => isFilled('lastName')},
                                      }, {
                                          details: {md: 12}
                                      }, {
                                          'description': {
                                              md: 12,
                                              optional: ({isFilled}) => isFilled('lastName'),
                                              render: (props) => {
                                                  const {data, errorSchema} = props
                                                  const {firstName, lastName} = data

                                                  return (
                                                      <div>
                                                          <h3>Hello, {firstName} {lastName}!</h3>
                                                          <p>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy
                                                              eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam
                                                              voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet
                                                              clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit
                                                              amet. Lorem ipsum dolor sit amet, consetetur sad</p>
                                                      </div>
                                                  )
                                              }
                                          }
                                      }, {
                                          age: {md: 12, optional: ({isTrue}) => isTrue('details')}
                                      }, {
                                          bio: {md: 12, optional: ({isTrue}) => isTrue('details')}
                                      }
                                  ]
                              },
                              $user: {
                                  widget: {
                                      layout: [
                                          {username: {md: 12}}, {password: {md: 12}},
                                      ]
                                  }
                              },
                              $description: {}
                          }}
                          eventSchema={{
                              onRemoveKey: ({removeKey}) => removeKey(),
                              onAddKey: () => ({1: "1"})
                          }}
                          onChange={console.log} onBlur={() => console.log("blur")} onFocus={() => console.log("focus")}/>
                </Content>
            </AntdLayout>
        </Content>
    </AntdLayout>
}